import { Component } from "react";
import { options } from "../Object/Opr";
import StyledSelect from "./StyledSelect";

class UIOption extends Component {
  constructor(props) {
    super(props);
    let stateTmp = {};
    this.date = {};
    this.props.opr.args.forEach((e) => {
      stateTmp[e.getName()] = e.getValue();
      this.date[e.getName()] = e;
    });
    this.state = stateTmp;

    this.handlerChange = this.handlerChange.bind(this);
  }
  handlerChange(e) {
    let id = e.target.getAttribute("data-id");
    this.setState((oldState) => {
      if (this.date[id].getTypeCheckbox()) oldState[id] = !oldState[id];
      else oldState[id] = e.target.value;
      this.date[id].setValue(oldState[id]);
      return { ...oldState };
    });
  }
  render() {
    let res = [];
    this.props.opr.args.forEach((e) => {
      // console.log(e);
      let type = e.getTypeCheckbox() ? "checkbox" : "text";
      res.push(
        <div
          key={`${this.props.opr.getLabel()}_${e.getName()}_wrap`}
          className="opt-input"
        >
          <label
            // key={`${e.getName()}_lab`}
            htmlFor={`${e.getName()}_${this.props.opr.getLabel()}`}
          >
            {e.getName()}
          </label>
          <input
            data-id={e.getName()}
            id={`${e.getName()}_${this.props.opr.getLabel()}`}
            // key={`${e.getName()}_input`}
            type={type}
            autoComplete={"off"}
            defaultChecked={this.state[e.getName()]}
            onChange={this.handlerChange}
          />
        </div>
      );
    });
    return (
      <div
        style={{ display: this.props.bool ? "" : "none" }}
        className="opt-wrap"
      >
        {res}
      </div>
    );
  }
}

class UIOperation extends Component {
  constructor(props) {
    super(props);
    this.options = options;
  }

  render() {
    let oprs = [];
    this.options.forEach((e) => {
      oprs.push(
        <UIOption key={e.getLabel()} bool={this.props.opr === e} opr={e} />
      );
    });
    return (
      <>
        <div className="opr-wrap">
          <StyledSelect
            options={this.options}
            values={[]}
            onChange={this.props.handlerChange}
          />
          <span
            className="opr-help disable-select"
            onClick={() => this.props.changeHelp(true && this.props.opr)}
          >
            💡
          </span>
        </div>
        {oprs}
      </>
    );
  }
}

export default UIOperation;
