class Arg {
  constructor(name, value, type = false) {
    this.name = name;
    this.value = value;
    this.type = type;
  }
  getName() {
    return this.name;
  }
  getValue() {
    return this.value;
  }
  getTypeCheckbox() {
    return this.type;
  }

  setValue(newValue) {
    this.value = newValue;
  }
}

class Opr {
  constructor(id, label, doc, args = []) {
    this.value = id;
    this.label = label;
    this.doc = doc;
    this.args = args;
  }

  getLabel() {
    return this.label;
  }

  getDoc() {
    return this.doc;
  }

  getArgs() {
    return this.args;
  }

  exec() {
    return null;
  }
}

export class OprLength extends Opr {
  constructor() {
    super(
      1,
      "Length",
      "La longueur d'une chaine de caractères, exprimée en nombre de points de code UTF-16",
      [new Arg("NoCount", "")]
    );
  }

  exec(str) {
    return str.replaceAll(this.args[0].getValue(), "").length;
  }
}

export class OprCount extends Opr {
  constructor() {
    super(2, "Count", "Conte le nombre de chaine CountString dans Input", [
      new Arg("CountString", ""),
    ]);
  }

  exec(str) {
    const regexp = new RegExp(this.args[0].getValue(), "g");
    return [...str.matchAll(regexp)].length;
  }
}

export class OprIndexOf extends Opr {
  constructor() {
    super(
      3,
      "IndexOf",
      "renvoie l'indice de la première occurence de la valeur cherchée au sein de la chaîne courante. Elle renvoie -1 si la valeur cherchée n'est pas trouvée",
      [new Arg("String", "")]
    );
  }

  exec(str) {
    return str.indexOf(this.args[0].getValue());
  }
}

export class OprLastIndexOf extends Opr {
  constructor() {
    super(
      4,
      "LastIndexOf",
      "renvoie l'indice, dans la chaîne courante, de la dernière occurence de la valeur donnée en argument. Si cette sous-chaîne n'est pas trouvée, la méthode renvoie -1",
      [new Arg("String", "")]
    );
  }

  exec(str) {
    return str.lastIndexOf(this.args[0].getValue());
  }
}

export class OprReplaceAll extends Opr {
  constructor() {
    super(
      5,
      "ReplaceAll",
      "retourne une nouvelle chaîne de caractères dans laquelle toutes les occurrences d'un motif donné ont été remplacées par une chaîne de remplacement",
      [new Arg("Substr", ""), new Arg("NewSubstr", "")]
    );
  }

  exec(str) {
    return str.replaceAll(this.args[0].getValue(), this.args[1].getValue());
  }
}
export class OprToLocaleLowerCase extends Opr {
  constructor() {
    super(
      6,
      "ToLocaleLowerCase",
      "Renvoie la valeur de la chaîne de caractères, convertie en minuscules",
      []
    );
  }

  exec(str) {
    return str.toLocaleLowerCase();
  }
}

export class OprToLocaleUpperCase extends Opr {
  constructor() {
    super(
      7,
      "ToLocaleUpperCase",
      "Renvoie une conversion en majuscules de la chaîne de caractères courante",
      []
    );
  }

  exec(str) {
    return str.toLocaleUpperCase();
  }
}

export class OprToLocaleCapitalizeCase extends Opr {
  constructor() {
    super(
      8,
      "ToLocaleCapitalizeCase",
      "Renvoie une conversion de la première lettre de chaque mot en capitale. Les autres caractères ne seront pas modifiés et conserveront ainsi la forme originale",
      []
    );
  }

  exec(str) {
    let res = "";
    str.split(" ").forEach((e) => {
      e = e.replace(e[0], e[0].toLocaleUpperCase());
      res += e + " ";
    });
    return res;
  }
}

export class OprTrim extends Opr {
  constructor() {
    super(
      9,
      "OprTrim",
      "Retire les blancs en plus de la chaîne de caractères",
      []
    );
  }

  exec(str) {
    let res = str.trim();
    res = res.replaceAll(/\t+/g, "\t");
    res = res.replaceAll(/[ ]+/g, " ");
    res = res.replaceAll(/\n+/g, "\n");
    return res;
  }
}

export const options = [
  new OprLength(),
  new OprCount(),
  new OprIndexOf(),
  new OprLastIndexOf(),
  new OprReplaceAll(),
  new OprToLocaleLowerCase(),
  new OprToLocaleUpperCase(),
  new OprToLocaleCapitalizeCase(),
  new OprTrim(),
];
