import { Component, createRef } from "react";
import UIButton from "./UIButton";

class InOut extends Component {
  constructor(props) {
    super(props);

    this.refVal = createRef();
    this.refRes = createRef();
    this.state = {
      value: "",
      res: "",
    };

    this.handlerChangeV = this.handlerChangeV.bind(this);
    this.handlerChangeR = this.handlerChangeR.bind(this);
    this.handlerClickRes = this.handlerClickRes.bind(this);
    this.handlerClickOpr = this.handlerClickOpr.bind(this);
  }

  handlerChangeV(e) {
    this.setState({ value: e.target.value });
  }
  handlerChangeR(e) {
    this.setState({ res: e.target.value });
  }

  handlerClickRes(e, alert) {
    this.refRes.current.select();
    this.refRes.current.setSelectionRange(0, 99999);
    document.execCommand("copy");
    alert.show("Copy");
  }

  handlerClickOpr(e, alert) {
    if (this.props.opr) {
      if (this.props.speed)
        navigator.clipboard
          .readText()
          .then((text) => {
            this.setState({
              value: text,
              res: this.props.opr.exec(text),
            });
            alert.show("Executed Successfully");
            this.handlerClickRes(e, alert);
          })
          .catch((err) => {
            console.error("Failed to read clipboard contents: ", err);
            alert.show(`Failed to read clipboard contents`, {
              type: "error",
            });
          });
      else {
        this.setState({ res: this.props.opr.exec(this.state.value) });
        alert.show("Executed Successfully");
      }
    } else {
      alert.show(`Failed to read clipboard contents`, {
        type: "error",
      });
    }
  }

  render() {
    return (
      <div className="operation">
        <div className="opt-wrapInput">
          <textarea
            id="input1"
            placeholder="Inupt"
            value={this.state.value}
            onChange={this.handlerChangeV}
            ref={this.refVal}
          />
          <UIButton
            onClick={this.handlerClickOpr}
            className="disable-select"
            // text={`run ${this.props.speed ? "🔥" : "!!"}`}
            text={
              <>
                run
                {this.props.speed ? (
                  <span className="speed-wrap-circle">
                    <span className="speed-circle"></span>🔥
                  </span>
                ) : (
                  ""
                )}
              </>
            }
          />
        </div>
        <div className="opt-wrapOutput">
          <textarea
            id="input2"
            placeholder="Output"
            value={this.state.res}
            onChange={this.handlerChangeR}
            ref={this.refRes}
          />

          {!this.props.speed ? (
            <UIButton
              onClick={this.handlerClickRes}
              className="disable-select"
              text="Copy"
            />
          ) : (
            ""
          )}
        </div>
      </div>
    );
  }
}

export default InOut;
