import "../Style/style.css";
import logo from "../logo.png";
import { Component } from "react";
import InOut from "./InOut";
import UIOperation from "./UIOperation";
import { transitions, positions, Provider as AlertProvider } from "react-alert";
import UIButton from "./UIButton";
require("typeface-montserrat");

const stylePopUp = {
  position: positions.TOP_RIGHT,
  // timeout: 0,
  timeout: 2300,
  offset: "5px",
  type: "success",
  transition: transitions.FADE,
};

const AlertTemplate = ({ style, options, message, close }) => (
  <div className={`popup-wrap popup-${options.type}`} style={style}>
    <span
      className={`popup-emoji ${options.type === "info" ? "popup-active" : ""}`}
    >
      {options.type === "info" && "😒"}
    </span>
    <span
      className={`popup-emoji ${
        options.type === "success" ? "popup-active" : ""
      }`}
    >
      {options.type === "success" && "😀"}
    </span>
    <span
      className={`popup-emoji ${
        options.type === "error" ? "popup-active" : ""
      }`}
    >
      {options.type === "error" && "🥺"}
    </span>
    <span className="popup-message">{message}</span>
    <button className="popup-close" onClick={close}>
      <i className="fa fa-close"></i>
    </button>
  </div>
);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opr: null,
      help: false,
      speed: false,
    };
    this.handlerChange = this.handlerChange.bind(this);
    this.changeHelp = this.changeHelp.bind(this);
    this.activeSpeed = this.activeSpeed.bind(this);
  }
  handlerChange(newValue) {
    // console.log(newValue);
    this.setState({ opr: newValue[0] });
  }
  activeSpeed(e, alert) {
    this.setState({ speed: !this.state.speed });
    if (!this.state.speed)
      alert.show(`Limitez voutre vitesse`, {
        type: "info",
      });
    else
      alert.show(`Vous avez fait un bon choix`, {
        type: "info",
      });
  }
  changeHelp(val) {
    this.setState({ help: val });
  }
  render() {
    return (
      <AlertProvider template={AlertTemplate} {...stylePopUp}>
        <div className="main">
          <div className="heading">
            <img src={logo} alt="Logo" />
            <h1>Tools</h1>
          </div>
          {this.state.speed ? (
            <div className="message-danger">
              <h5>2 minutes gagnees, une vie de perdue...</h5>
              <div className="message-danger-small">
                Limitez voutre vitesse, pas la vie des autres !
              </div>
            </div>
          ) : (
            ""
          )}
          {this.state.help ? (
            <div className="help-mask" onClick={() => this.changeHelp(false)}>
              <div className="help-wrap">
                <h2>{this.state.opr.getLabel()}</h2>
                <p>{this.state.opr.getDoc()}</p>
              </div>
            </div>
          ) : (
            ""
          )}
          <UIButton
            className={`speed disable-select ${
              this.state.speed ? "speed-active" : ""
            }`}
            onClick={this.activeSpeed}
            text={
              <>
                speed
                {this.state.speed ? (
                  <span className="speed-wrap-circle">
                    <span className="speed-circle"></span>🔥
                  </span>
                ) : (
                  ""
                )}
              </>
            }
          ></UIButton>
          <div className="container">
            <UIOperation
              handlerChange={this.handlerChange}
              changeHelp={this.changeHelp}
              opr={this.state.opr}
            />
            <InOut opr={this.state.opr} speed={this.state.speed} />
          </div>
        </div>
      </AlertProvider>
    );
  }
}

export default App;
